@import "index";

.title {
  font-size: 1.4em; }

.archive {
  font-size: 1.1em; }

.archive time {
  display: inline-block;
  min-width: 10ch;
  margin: 0 .2em; }

.archive li {
  list-style: none; }

.icon {
  height: 1.5em;
  width: 1.5em;
  fill: currentColor;
  transition: .2s color;
  vertical-align: middle;
  margin-bottom: .15em; }

a:hover .icon {
  color: reduce(100); }


